import { AdalConfig, adalFetch, AuthenticationContext } from 'react-adal';

const endpoint = 'https://wbengineering.sharepoint.com/';
const prodAppId = '9fa8cac6-4668-449a-9232-f48c60a057c1';
const localhostAppId = 'c23fa0bc-275e-45a9-a518-b33893062e6b';

export const adalConfig: AdalConfig = {
    cacheLocation: 'localStorage',
    clientId: process.env.NODE_ENV !== 'production' ? prodAppId : localhostAppId,
    endpoints: {
        api:endpoint
    },
    postLogoutRedirectUri: window.location.origin,
    tenant: 'wbengineering.onmicrosoft.com'
};

export const authContext = new AuthenticationContext(adalConfig);

export const adalApiFetch = (url: string, options: any) => {
    const headers: any = {};
    if(options.headers && typeof options.headers[Symbol.iterator] === 'function') {
        const entries = options.headers.entries();
        let item: any = {};
        do {
            item = entries.next();
            const value = item.value;
            if(value) {
                headers[value[0]] = value[1];
            }
        } while(!item.done)
    }

    return adalFetch(authContext, endpoint, fetch, url, { ...options, headers });
}


